        this.$refs.multipleTable.clearSelection(); //点击取消后取消当前选中的这一项
      },
      /**
       * 回收 校验回收的流程是否是 审批中
       */
      withdraw(){
        if(this.multipleSelection[0].status != 2) {
          this.$message.warning('只能回收审批中的流程！');
          //取消当前选中项
          this.$refs.multipleTable.clearSelection();
          return;
        }
        util.burialPoint('我的已办-操作-回收按钮');
        //保存 回收 的流程 procInstID
        this.currentId = this.multipleSelection[0].procInstID
        this.showremove = true;
      },
      /**
       * 回收 确定
       */
      close(){
        this.showremove = false;
        this.$axios.delete('/api/recyclingButton',{data: {"procInstID": this.multipleSelection[0].procInstID}}).then(res => {
          let data = res.data;
          if(data.messageList[0].key == "01") {
            for (let i = 0; i < this.activeList.length; i++) {
              //前端 删除 已经 回收过的流程。 注意：回收后台是不会立刻执行成功，所以不能刷新页面
              if (Number(this.activeList[i].procInstID) === Number(this.currentId)) {
                this.activeList.splice(i, 1);
                this.total=this.total-1;
                this.$message.success(`${data.messageList[0].message} 【回收请求已发出，10分钟后请再刷新确认】`);
              }
            }
          }else if(data.messageList[0].key == "04"){
            this.$message.warning(`回收失败：${data.messageList[0].message}`);
            this.$refs.multipleTable.clearSelection();//催办成功后取消当前选中的这一项
          }else{
            this.$message.error(`回收失败：${data.messageList[0].message}`);
          }
        }).catch((err)=>{
          this.$message.error(`回收失败！${err}`);
          this.$refs.multipleTable.clearSelection();
          this.showremove = false;
        })
      },
      /**
       * 回收 取消
       */
      cancelremove(){
        this.showremove =false;
        this.$refs.multipleTable.clearSelection();//成功后取消当前选中的这一项
      },
      /**
       * 刷新查询条件
       */
      refresh(){
        this.CriteriaModel.ProcessNum='';
        this.serachSlectData.tagsOwnProcess = [];
        this.serachSlectData.tagsOwnCategory = [];
        this.CriteriaModel.ProcessCategory = [];
        this.CriteriaModel.Originator = '';
        this.CriteriaModel.DepartmentId = '';
        this.CriteriaModel.FormSubject = '';
        this.serachSlectData.tagsOrgSelect =[];
        this.serachSlectData.tagsOwnsUser = [];
        this.serachSlectData.CurrentUser = [];
        this.model.startDate = []; //发起时段
        this.model.handtDate = []; //发起时段
        this.model.finshDate = []; //发起时段
        this.CriteriaModel.ProcessStatus = this.state[0].value
      },
      /**
       * 金额输入中验证
       */
      inputValidate(position, value) {
        let that = this;
        let regPos = /^([0-9]*)([.]?|(\.\d{1,2})?)$/; //验证金额正则
        if (position == 'front') {
          if (!regPos.test(value)) {
            setTimeout(function () {
              that.model.whereParams.amountOfMoneyB = '';
            }, 100);
          }
        } else if (position == 'back') {
          if (!regPos.test(value)) {
            setTimeout(function () {
              that.model.whereParams.amountOfMoneyE = '';
            }, 100);
          }
        }
      },
      /**
       * 金额输完验证
       */
      checkValidate(position, value) {
        value = parseFloat(value);
        let that = this;
        if (position == 'front') {
          if (this.model.whereParams.amountOfMoneyE != '' && value > parseFloat(this.model.whereParams.amountOfMoneyE)) {
            that.model.whereParams.amountOfMoneyB = '';
          }
        } else if (position == 'back') {
          if (this.model.whereParams.amountOfMoneyB != '' && value < parseFloat(this.model.whereParams.amountOfMoneyB)) {
            that.model.whereParams.amountOfMoneyE = '';
          }
        }
      },
      /**
       * 查询
       */
      search() {
        util.burialPoint('我的已办-输入条件-搜索按钮');
        this.pageIndex = 1; // 页码重置
        this.getList(); // 获取列表数据
      },
      /**获取列表数据*/
      getList() {
        var that = this;
        //开始时间
        that.model.startDate == null ? (that.model.startDate = []) : "";
        //处理时间
        that.model.handtDate == null ? (that.model.handtDate = []) : "";
        //结束时间
        that.model.finshDate == null ? (that.model.finshDate = []) : "";

        that.CriteriaModel.SubmitStartDate  = that.model.startDate[0]? `${that.model.startDate[0]} 00:00:00`: "";
        that.CriteriaModel.SubmitEndDate  = that.model.startDate[1]? `${that.model.startDate[1]} 23:59:59`: "";

        that.CriteriaModel.ProcessingStartDate  = that.model.handtDate[0]? `${that.model.handtDate[0]} 00:00:00`: "";
        that.CriteriaModel.ProcessingEndDate   = that.model.handtDate[1]? `${that.model.handtDate[1]} 23:59:59`: "";

        that.CriteriaModel.FinishStartDate  = that.model.finshDate[0]? `${that.model.finshDate[0]} 00:00:00`: "";
        that.CriteriaModel.FinishEndDate  = that.model.finshDate[1]? `${that.model.finshDate[1]} 23:59:59`: "";

        that.CriteriaModel.ProcessNum = that.CriteriaModel.ProcessNum.replace(/\s+/g,"");
        that.CriteriaModel.FormSubject = that.CriteriaModel.FormSubject.replace(/\s+/g,"");

        //处理人
        if( that.serachSlectData.CurrentUser[0]){
          that.CriteriaModel.CurrentUser =that.serachSlectData.CurrentUser[0].UserAccount;
        }
        //申请人
        if(that.serachSlectData.tagsOwnsUser[0]){
          that.CriteriaModel.Originator = that.serachSlectData.tagsOwnsUser[0].UserAccount;
        }else {
          that.CriteriaModel.Originator = ''
        }
        //部门
        if( that.serachSlectData.tagsOrgSelect[0]){
          that.CriteriaModel.DepartmentId = that.serachSlectData.tagsOrgSelect[0].UserAccount;
        }else {
          this.CriteriaModel.DepartmentId = '';
        }
//        //流程
        if( that.serachSlectData.tagsOwnProcess){
          that.CriteriaModel.ProcessCode = [];
          for(let item of this.serachSlectData.tagsOwnProcess){
            that.CriteriaModel.ProcessCode.push(item.processCode);
          }
        }
        //流程类别
        if(that.serachSlectData.tagsOwnCategory.length >0){
          that.CriteriaModel.ProcessCategory = [];
          for(let v of that.serachSlectData.tagsOwnCategory){
            that.CriteriaModel.ProcessCategory.push(v.id)
          }
        }
        that.imgLoading = true;
        // 请求服务器获取已办数据  CriteriaModel
        that.$axios({
          url:'/Portal/WorkFlow/GetMyInvolveds',
          data:that.CriteriaModel,
          method:'post'
        }).then((result) => {
          if(result.data.code === 200) {
            // 已办数据集合
            that.activeList = result.data.data.item;
            for (let item of that.activeList){
              item.FinishDate =  item.FinishDate ? moment(item.FinishDate).format("YYYY-MM-DD HH:mm") : '';
              if(item.ApprovalUsers){
                item.ApprovalUsers = item.ApprovalUsers.split(",");
              }
            }
            // 数据总数
           that.CriteriaModel.total = result.data.data.total;
          }else{
            that.$message.error("请求失败！");
          }
          that.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading();
          that.imgLoading = false;
//          that.$message.error(`审批失败!ERROR MESSAGE:${err.response.data.message}`);
        })
      },
      /**
       * 页面size变更方法
       */
      handleSizeChange(val) {
        util.burialPoint('我的已办-列表-每页显示条数按钮');
        var likeThis = this;
        var pageSize = `${val}`;
        likeThis.PageIndex = 1;
        likeThis.CriteriaModel.PageSize = parseInt(pageSize);
        localStorage.donePageSize = val;
        likeThis.getList();
      },
      /**
